/* This file is used for fallback styles and will be overridden by styled-components */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&family=Raleway:wght@700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  line-height: 1.6;
  overflow-x: hidden;
  background-color: #ffffff;
  color: #272829;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Raleway', sans-serif;
  font-weight: 700;
  margin-bottom: 15px;
}

a {
  text-decoration: none;
  color: #0563bb;
} 